import categoriesConstatnts from "./constants";

let initialState = {
    data: {},
    isLoading: false,
};

const loadCategories = (state, action) => {
    const { payload, page } = action;
    let newState = {};
    if (page > 0) {
        newState = Object.assign({}, state);
    } else {
        newState = { data: {} };
    }
    for (const key in payload) {
        newState.data[payload[key]._id] = payload[key];
    }
    return newState;
};

export default (state = initialState, action) => {
    switch (action.type) {
        case categoriesConstatnts.CATEGORIES_FETCH_SUCCESS:
            return loadCategories(state, action);
    }
    return state;
}