import productServices from "../../services/api/products";
import productsConstatnts from "./constants";

const loading = (bool) => {
    return {
        type: productsConstatnts.PRODUCTS_LOADING,
        isLoading: bool,
    };
};

const addProduct = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await productServices.createProduct(params);
        dispatch({
            type: productsConstatnts.PRODUCT_CREATE_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const getAllProducts = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await productServices.getAllProducts(params);
        dispatch({
            type: productsConstatnts.PRODUCTS_FETCH_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const productsActions = {
    getAllProducts,
    addProduct,
}

export default productsActions;


