import { Button } from '@material-ui/core'
import { useState, useEffect } from 'react'
import './map.css'
import { Link, NavLink, useLocation } from 'react-router-dom';
import locationServices from '../../../services/api/locations';
import { keys } from '@material-ui/core/styles/createBreakpoints';
export const Waypoints = ({ end, setEnd, myCoords, waypoints, setWaypoints, start, setStart, width, setDirections }) => {
  console.log(width)
  const waypts = [];
  const cities = [];
  var citiesUnique = [];
  var citiesUniqueSorted = []

  const [locations, setLocations] = useState([]);
  const [locationsFiltered, setLocationsFiltered] = useState([]);
  const [limit, setLimit] = useState(1000);
  const [page, setPage] = useState(1);
  const [reload, setReload] = useState((state) => !state)
  const [cityFilter, setCityFilter] = useState([])
  const [Waypoints, SETWaypoints] = useState()
  const queryParams = new URLSearchParams(useLocation().search)

  for (var i in locations) {
    cities.push(locations[i].City)
  }
  var citiesUnique = cities.filter(function (elem, pos) {

    return cities.indexOf(elem) == pos;

  });
  var citiesUniqueSorted = citiesUnique.sort((a, b) => a !== b ? a < b ? -1 : 1 : 0);

  useEffect(() => {
    getData()
    return () => {
    }
  }, [])

  useEffect(() => {
    getDataFiltered()
    return () => {
    }
  }, [reload])


  const RoadPlanner = { 
    "GPS_COORDS": myCoords,
    "start": start,
    "end": end,
    "waypoints": waypoints,
  }

  console.log("RoadPlanner", RoadPlanner)

  return (
    <div id="right-panel">
      <div>
        <b>Start:</b>
        <br />
        <select onChange={(e) => setStart(e.target.value)}>
          <option value={myCoords}>My Location {JSON.stringify(myCoords)}</option>
          <option value={"Przewz 9, Krakw, Polska"} >Office</option>
          <option value={"Chopska 12, Krakw, Polska"} >Home</option>
        </select>
        <br />
        <b>Select City: {citiesUnique.length} CITIES AVAILABLE</b>
        <select component={Link} to={`?City=${cityFilter}`} onChange={(e) => { setCityFilter(e.target.value); RELOAD(); }} >
          <option>SELECT CITY</option>
          {citiesUniqueSorted.map((item) => (<>
            <option component={Link} to={`?City=${item}`} value={item}>{item}</option>
          </>))}
        </select>
        <b>Select Waypoints: {locationsFiltered.length} WAYPOINTS AVAILABLE</b><br/>
        <i>(Ctrl+Click or Cmd+Click for multiple selection)</i> <br />
        <select multiple id={"waypoints"} onChange={() => { setWaypoint() }}>
          {locationsFiltered.map((item) => (<>
            <option
              key={item._id}
              value={`${item.Name},${item.Street_Address},${item.City},${item.Country_code}`}
            >{item.Name} - {item.Street_Address},{item.City}  --  ostatnie odwiedziny: {item.Time_of_last_activity} </option>
          </>))}

        </select>
        <br />
        <b>End:</b>
        <br />
        <select onChange={(e) => setEnd(e.target.value)} id="end">
          <option value={"Przewz 9, Krakw, Polska"} >Office</option>
          <option value={"Chopska 12, Krakw, Polska"} >Home</option>

          {locationsFiltered.map((item) => (<>
            <option
              key={item._id}
              value={`${item.Name},${item.Street_Address},${item.City},${item.Country_code}`}
            >{item.City} - {item.Street_Address}   -    {item.Name}  --  ostatnie odwiedziny: {item.Time_of_last_activity} </option>
          </>))}

        </select>
        <br />
      </div>
      <div id="directions-panel">
        {waypoints ? waypoints.map((point, index) => (<>
          <div style={{ display: 'flex' }} key={point.location}> <p>{point.location}</p>
            <Button onClick={() => {
              setWaypoints(waypoints.splice(index, index === index))
              console.log("point,index", point, index)
            }}>
              Delete {index}
            </Button>
          </div>
        </>)) : <></>}
      </div>
      <div id={"actions"}>
        <Button color={"secondary"} id={"setWaypoints"} variant="contained" onClick={() => setWaypoints([])} >delete Waypoints</Button>
        <Button color={"primary"} id={"setDirections"} variant="contained" onClick={() => setDirections(true)} >set Directions</Button></div>
    </div>)

  function RELOAD() {
    setReload((state) => !state);
  }

  async function getData() {
    const result = await locationServices.getAllLocationsFilters();
    setLocations(result)
  }
  async function getDataFiltered() {
    const getUrlParams = new URLSearchParams({
      limit: queryParams.get("limit") || limit,
      page: queryParams.get("page") || page,
      City: queryParams.get("City") || cityFilter,
    });
    const result = await locationServices.getAllLocations(getUrlParams);
    setLocationsFiltered(result)

  }

  function setWaypoint() {
    const checkboxArray = document.getElementById("waypoints");
    for (let i = 0; i < checkboxArray.length; i++) {
      if (checkboxArray.options[i].selected) {
        waypoints.push({
          location: checkboxArray[i].value,
          stopover: true,
        });
      }
    }
    console.log(waypts)
    SETWaypoints(waypts)
  }
  //   function deleteWaypoint(index) {
  //    for( var i = 0; i < waypoints.length; i++){ 

  //     if ( waypoints[i] === index) { 

  //         waypoints.splice(i, index); 
  //     }

  // }}



}