import React, { useEffect, useState } from 'react';
import cartServices from '../../../services/api/cart';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import icon from '../../../assets/icons';
import { styles, bgColorD } from './styles';
import './CartItem.css'

export const CartItem = ({
    id,
    onClick,
    product_image,
    product_name,
    product_id,
    quantity,
    product_price,
    total_price,
    width,
    // cartData
}) => {




    const [amount, setAmount] = useState(quantity);

    const subtotal = {
        tax: (product_price * 1.23) - product_price,
        totalTax: (total_price * 1.23) - total_price,
        totalPriceWithTax: total_price * 1.23,
    }

    const productOrderDetails = {
        product_id: product_id,
        product_name: product_name,
        quantity: amount,
        product_image: product_image,
        product_price: product_price,
        total_price: amount * product_price
    }

    const preOrderList = [];
    const addToPreOrderList = (productOrderDetails) => {
        preOrderList.push(productOrderDetails);
    };

    const handleAddAmount = async () => {
        setAmount(amount + 1);
        updateProductInCart();
    }

    const handleSubstractAmount = () => {
        setAmount(amount - 1);
        updateProductInCart();
    }

    async function handleDeleteProduct() {
        cartServices.deleteProduct(id)
    }

    async function updateProductInCart() {
        cartServices.updateCart(id, productOrderDetails);

    }

    useEffect(() => {
        updateProductInCart()
    }, [amount]);



    return (<>
        {width < 750 ?
            (<div style={styles.main} key={id}>
                <div style={styles.image}>
                    <img style={styles.imageIMG} onClick={onClick} src={product_image} />
                </div>
                <div style={styles.column}>

                    <div style={{
                        display: "flex", flexDirection: "row", width: "100%",
                        height: "20%", justifyContent: "space-between", border: "1px solid rgba(1, 39, 36, 0.9)",
                        backgroundColor: bgColorD,
                    }}>
                        <div style={styles.madeBy}>made by</div>
                        <div style={{ display: "flex", flexDirection: "row" }}>
                            <button onClick={() => handleDeleteProduct()} style={styles.DeleteBtn}>
                                <FontAwesomeIcon icon={icon.faTrashAlt} size={32} />
                            </button>
                        </div>
                    </div>

                    <div style={styles.productName}>{product_name}</div>
                    <div style={{
                        display: "flex", flexDirection: "row", width: "100%",
                        height: "20%", justifyContent: "space-between", border: "1px solid rgba(1, 39, 36, 0.9)",
                        backgroundColor: bgColorD,
                    }}>
                        <div style={styles.productPrice}>Amount: {amount}</div>
                        <div style={{ display: "flex", flexDirection: "row" }}>

                            <button onClick={(e) => { handleSubstractAmount(e) }} style={styles.quanitySetBtnMinus}>

                                <FontAwesomeIcon icon={icon.faMinus} size={32} />
                            </button>


                            <input value={amount} onChange={(e) => { e.preventDefault(); setAmount(e.target.value); }} style={styles.quanitySetInput} />
                            <button onClick={(e) => { handleAddAmount(e) }} style={styles.quanitySetBtnPlus}>
                                <FontAwesomeIcon icon={icon.faPlus} size={32} />
                            </button>
                        </div>
                    </div>
                    <div style={styles.totalPrice}>{product_price}*{productOrderDetails.quantity} = {productOrderDetails.total_price.toFixed(2)}</div>
                </div>
            </div>) :
            (<div style={styles.mainDesktop} key={id}>
                <div style={styles.imageDesktop}>
                    <img style={styles.imageIMGdesktop} onClick={onClick} src={product_image} />
                </div>
                <div style={styles.column, { width: "30%", overflowY: "scroll" }}>
                    <div style={styles.madeByDesktop}>made by</div>
                    <div style={styles.productNameDesktop}>{product_name}</div>
                </div>
                <div style={styles.productPriceDesktop}>{product_price}</div>
                <div style={styles.productPriceDesktop}>{subtotal.tax.toFixed(2)}</div>
                <div style={styles.quanitySetDesktop}>
                    <div style={styles.row}>
                        <input placeholder={amount} value={() => amount} onChange={(e) => setAmount(e.target.value)} style={styles.quanitySetInputDesktop} />
                        <button onClick={() => handleSubstractAmount()} style={styles.quanitySetBtnMinusDesktop}>
                            <FontAwesomeIcon icon={icon.faMinus} size={32} />
                        </button>
                        <button onClick={() => handleAddAmount()} style={styles.quanitySetBtnPlusDesktop}>
                            <FontAwesomeIcon icon={icon.faPlus} size={32} />
                        </button>
                    </div>
                </div>
                <div style={styles.quanityDesktop}>{amount}</div>
                <div style={styles.productPriceDesktop}>
                    <div >{ }</div>
                    <div >{((productOrderDetails.total_price * 1.23) - productOrderDetails.total_price).toFixed(2)}</div>
                </div>
                <div style={styles.quanityDesktop}>
                    <div >{productOrderDetails.total_price.toFixed(2)}</div>
                    <div >{(productOrderDetails.total_price * 1.23).toFixed(2)}</div>
                </div>
                <div style={styles.DeleteBtnDesktop}>
                    <button onClick={() => handleDeleteProduct()} style={{ width: "100%", height: "100%", fontSize: 32, display: "flex", flexDirection: "column", justifyContent: "center" }}>
                        <FontAwesomeIcon icon={icon.faTrash} style={{ alignSelf: "center" }} />
                        <p style={{ fontSize: 16, alignSelf: "center" }}>DELETE</p>
                    </button>
                </div>
            </div>)

        }
    </>)
}
export default CartItem;
