import React, { useEffect, useState } from 'react';
import icon from '../../../assets/icons';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import { renderToString } from "react-dom/server";
import JconButton from '../iconButton/iconButton';
import { styles } from '../cart/styles';
import ReactPDF from '@react-pdf/renderer';
import { PDFViewer } from '@react-pdf/renderer';
import Pdf from '../pdf/pdf';
import { routes } from '../../../routes';
import { broweserHistoryBack } from '../../../globals';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCross } from '@fortawesome/free-solid-svg-icons';



const CartFooter = ({ cart, width, confirmOrder, totalPrice, reload }) => {
    const [noPdfAlert, setNoPdfAlert] = useState(false)
    const NoPdfAlert = () => {
        return (<>
            <div style={{ position: "fixed", width: width, top: "33%", height: "17%", backgroundColor: "whitesmoke", color: "black" }}>
                <h1>
                    no products!
        </h1>
            </div>
        </>)
    }

    const style = {
        checkout: {
            display: "flex",
            justifyContent: "space-evenly",
            alignItems: "center",
            position: "fixed",
            bottom: 0,
            left: 0,
            width: "100%",
            height: "48px",
            backgroundColor: "black",
            color: "white"
        },
        confirmOrder: {
            display: "flex",
            flexDirection: "column",
            justifyContent: "stretch",
            alignItems: "center",
            position: "fixed",
            top: 48,
            right: 0,
            bottom: 40,
            left: 0,
            backgroundColor: "lightgray",
            color: "white",
            overflowX: "hidden"

        },
        totalPrice: {
            // position: "fixed",
            // bottom: "15px",
            // left: "15px",
            fontSize: 18,
            backgroundColor: "black",
        }
    }
    const [order, setOrder] = useState(false)

    const generatePdf = () => {
        const pdf = new jsPDF('p', 'px',);
        pdf.autoTable({ html: '#mytable' })
        pdf.save(`order.pdf`)
    }
    const [TOTALQUANTITY, setTotalQuanity] = useState(0)
    let totalQuantity = []

    const handleQuantity = () => {
        Object.keys(cart).map(key => {
            totalQuantity.push(cart[key].quantity)
            console.log("totalQuanity", totalQuantity)
            const totalQUANTITY = totalQuantity.reduce(function (previousValue, currentValue, index, array) {
                return previousValue + currentValue;
            });
            console.log("totalQUANTITY", totalQUANTITY)

            setTotalQuanity(totalQUANTITY)

        }
        )
    }
    useEffect(() => {
        handleQuantity()
    }, [reload])
    return (<>

<button
                onClick={() => { broweserHistoryBack() }}
                // icon={icon.faCross}
                // iconAlign="center"
                // label="back"
                // labelAlign="center"
                // size="24px"
                // fontsize="8"
                // variant="column"
                style={{backgroundColor:"red",position:"fixed",top:0,right:0,zIndex:0}}
                // className="Toggle"
                // href="/cart"
                // label={cartLength}
                // lstyle={{ position: "absolute", bottom: 2.5, left: 2.5, backgroundColor: "yellow", color: "black", borderRadius: 25, width: 15, height: 15 }}
            ><FontAwesomeIcon icon={faCross} /*size="10x"*/  /></button>

        {order ?
            (<>
                <div style={style.confirmOrder}>
                    <table id="mytable" style={{
                        postition: "fixed", top: 48, bottom: 0, left: 0, right: 0, display: "flex", flexDirection: "column", width: width, height: "100%", overflowY: "scroll",
                        overflowX: "hidden"
                    }}>
                        <thead style={{ width: width, position: "sticky", top: 0 }}>
                            <tr style={{ width: width }}>
                                <td>Name</td>
                                <td>Amount</td>
                                <td>Netto</td>
                                <td>Gross</td>
                                <td>Vat</td>
                                <td>TNetto</td>
                                <td>TGross</td>
                            </tr>
                        </thead>

                        {Object.keys(cart).map(key => <>
                            <tr>
                                <td>{cart[key].product_name}</td>
                                <td><img style={{ width: "50px" }} src={cart[key].product_image} /></td>
                                <td>{cart[key].quantity}</td>
                                <td>{(cart[key].product_price).toFixed(2)}</td>
                                <td>{(cart[key].product_price * (1.23) - cart[key].product_price).toFixed(2)}</td>
                                <td>{(cart[key].total_price).toFixed(2)}</td>
                                <td>{(cart[key].total_price * 1.23).toFixed(2)}</td>
                            </tr></>)}
                        <tr
                            style={{
                                backgroundColor: "black",
                                // position:"fixed", bottom:90, left:0
                            }}
                        >
                            <td>Total Products</td>
                            <td></td>
                            <td>Total Quantity</td>
                            <td></td>
                            <td>Total Netto</td>
                            <td>Total Vat</td>
                            <td>Total Gross</td>
                        </tr>
                        <tr style={{
                            backgroundColor: "black",
                            // position:"fixed", bottom:40, left:0
                        }}>
                            <td>{Object.keys(cart).length}</td>
                            <td></td>
                            <td>{TOTALQUANTITY}</td>
                            <td></td>
                            <td>{((totalPrice * 1.23) - totalPrice).toFixed(2)}</td>
                            <td>{totalPrice.toFixed(2)}</td>
                            <td>{(totalPrice * 1.23).toFixed(2)}</td>
                        </tr>

                        <div style={{ position: "fixed", bottom: -10, left: 0, display: "flex", flexDirection: "row", justifyContent: "stretch", width: "100vw", height: 50 }}>
                            <JconButton
                                style={{ display: "flex", flexDirection: "row", justifyContent: "space-around", width: width / 3, height: 50 }}
                                label="Save Order"
                                size={"32px"}
                                icon={icon.faSave}
                                onClick={Object.keys(cart).length <= 0 ? () => setNoPdfAlert(true) : () => console.log('save file route')}
                            />
                            <JconButton
                                style={{ display: "flex", flexDirection: "row", justifyContent: "space-around", width: width / 3, height: 50 }}
                                label="Download PDF"
                                size={"32px"}
                                icon={icon.faFilePdf}
                                onClick={Object.keys(cart).length <= 0 ? () => setNoPdfAlert(true) : () => generatePdf()}
                            />
                            <JconButton
                                style={{ display: "flex", flexDirection: "row", justifyContent: "space-around", width: width / 3, height: 50 }}
                                label="Confirm Order"
                                size={"32px"}
                                icon={icon.faCheck}
                                onClick={Object.keys(cart).length <= 0 ? () => setNoPdfAlert(true) : () => confirmOrder()}
                            // href={routes.orders}
                            />
                        </div>

                    </table>
                    {noPdfAlert ? <NoPdfAlert /> : <></>}
                </div></>) : (<div style={style.checkout}>
                    <h1 style={style.totalPrice}>Total Price: {totalPrice.toFixed(2)} $</h1>
                    <JconButton
                        label="Checkout"
                        variant={"row"}
                        icon={icon.faMoneyBillWave}
                        size="28px"
                        // className="row"
                        onClick={() => setOrder((state) => !state)}
                    // style={{ backgroundColor: "red" }}
                    />
                </div>)}
    </>)
}
export default CartFooter;