import React, { useEffect, useState } from 'react';
import CartList from '../components/atoms/cart/CartList';
import CartFooter from '../components/atoms/cartFooter/cartFooter';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import cartActions from '../store/cart/action';
import cartServices from '../services/api/cart';
import { Redirect } from 'react-router';
import { routes } from '../routes';
import orderServices from '../services/api/orders';
const Cart = ({
    cart, getCart, width
}) => {
    const cartData = cart.data;
    const [totalPrice, setTotalPrice] = useState(0);

    const handleTotalPrice = () => {
        let total = []
        Object.keys(cartData).map(key => {
            total.push(cartData[key].total_price)
            console.log("total", total)
            const TOTAL = total.reduce(function (previousValue, currentValue, index, array) {
                return previousValue + currentValue;
            });
            console.log("TOTAL", TOTAL)

            setTotalPrice(TOTAL)
        }
        )
    }
    const confirmOrder = async () => {
        console.log(preOrderList);
        await orderServices.addOrder(preOrderList)
        await cartServices.deleteCart();
    }
    const preOrderList = {
        client: "client",
        seller: "seller",
        total_price: totalPrice.toFixed(2),
        total_discount: 5, //"customer[i].discount"
        status: "true",
        products: [cartData],
        date: Date().toLocaleString(),
    };
    console.log("preOrderList", preOrderList)
    console.log("cartData", cartData)
    console.log("totalPrice:", totalPrice)
    useEffect(() => { getCart(); handleTotalPrice(); }, [])
    return (<div style={{ position: "fixed", width: "100%", top: 48, bottom: 48, left: 0, right: 0, boxSizing: "border-box", overflowY: "scroll", overflowX: "hidden" }}>
        <CartList
            width={width}
        />
        <CartFooter handleTotalPrice={handleTotalPrice} cart={cartData} width={width} confirmOrder={confirmOrder} preOrderList={preOrderList} totalPrice={totalPrice} />
    </div>)
}

Cart.propTypes = {
    cart: PropTypes.func.isRequired,
}
const getData = { getCart: cartActions.getCartItems }


const mapStateToProps = (state) => {
    const { cart } = state;
    console.log(state);
    return { cart };
}
export default connect(mapStateToProps, getData)(Cart);