import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';


const JconButton = ({ style, className, border, padding, icon, iconAlign, href, rotate, borderRadius, background, color, size, fontsize, label, labelAlign, onClick, variant, btnwidth, btnheight,btnposition, btnbottom, lstyle }) => {
    return (
        <Link to={href}>
            <button onClick={onClick} className={className} style={style, {display: "flex", flexDirection: variant }
                // ,{ padding: padding, display: "flex", border: border, borderRadius: borderRadius, color: color, flexDirection: variant, width: btnwidth, height: btnheight, position:btnposition, bottom:btnbottom, zIndex:1}
                }>
                <i style={{ fontSize: size, alignSelf: "center", lineHeight: size }}>
                    <FontAwesomeIcon icon={icon} />
                </i>
                {label ? (<p style={lstyle,{alignSelf: "center", fontSize: 15, fontFamily:"sans-serif" }} >{label}</p>) : (<></>)}
            </button>
        </Link>
    )
}
export default JconButton;