import React, { useEffect, useState } from "react";
import { sliderGalery } from "../../../assets/data/images";

const ProductImageSlide = ({ images, delayTime, width, setGalleryList, onClick }) => {

    const [index, setIndex] = useState(0);
    const [autoPlayTimeout, setAutoPlayTimeout] = useState(delayTime);
    const [autoPlayOn, setAutoPlayOn] = useState(false);
    const [choosenImage, setChoosenImage] = useState([images[0]]);

    const slideLeft = () => {
        const nextIndex = index - 1;
        if (nextIndex < 0) {
            setIndex(images.length - 1);
        } else {
            setIndex(nextIndex);
        }
    };

    const autoPlay = () => {
        setTimeout(slideLeft, autoPlayTimeout);
    }

    // useEffect(() => {
    autoPlay();
    // })

    const styles = {
        root: {
            boxSizing: "border-box",
            width: "100%",
            height: "30%",
            backgroundColor: "white",
            transition: "all 2s ease-out",
            border: "1px solid black",
        },
        image: {
            boxSizing: "border-box",
            width: "100%",
            height: "30%",
            padding: " 25px 5px 25px 5px",
            objectFit: "scale-down",
            transition: "all 2s ease-in"
        },
        rootDesktop: {
            boxSizing: "border-box",
            width: "100%",
            height: "250px",
            backgroundColor: "white",
            border: "1px solid black",
            transition: "all 2s ease-out"

        },
        imageDesktop: {
            boxSizing: "border-box",
            width: "100%",
            height: "100%",
            padding: " 25px 5px 25px 5px",
            objectFit: "scale-down",
            transition: "all 2s ease-out"
        }
    }
    return (<>
        <div
            // onMouseEnter={()=> setTimeout(slideLeft, autoPlayTimeout)}
            style={width < 550 ? styles.root : styles.rootDesktop}>
            <img
                onContextMenu={(e) => e.preventDefault()}
                onClick={onClick} src={images[index]} style={width < 550 ? styles.image : styles.imageDesktop} alt={choosenImage} />
        </div>
    </>
    );
};

export default ProductImageSlide;