import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import zIndex from "@material-ui/core/styles/zIndex";
import React, { useEffect, useState } from "react";
import { sliderGalery } from "../../../assets/data/images";
import icon from "../../../assets/icons";
import useSwipe from "../../../scripts/useSwipe";

const ProductImageSlideThumbs = ({ images, width }) => {

    const [index, setIndex] = useState(0);
    const [choosenImage, setChoosenImage] = useState([images[0]]);
    const [galleryView, setGalleryView] = useState(false);

    const slideLeft = () => {
        const nextIndex = index - 1;
        if (nextIndex < 0) {
            setIndex(images.length - 1);
        } else {
            setIndex(nextIndex);
        }
    };
    const slideRight = () => {
        const nextIndex = index + 1;
        if (nextIndex > 0) {
            setIndex(images.length + 1);
        } else {
            setIndex(nextIndex);
        }
    };

    const bind = useSwipe({
        onUp: () => console.log("up"),
        onDown: () => console.log("down"),
        onRight: () => slideRight(),
        onLeft: () => slideLeft()
    });



    const styles = {
        root: {
            boxSizing: "border-box",
            width: "100%",
            height: "30%",
            backgroundColor: "white",
            transition: "all 2s ease-out",
            border: "1px solid black",
        },
        image: {
            boxSizing: "border-box",
            width: "100%",
            height: "30%",
            padding: " 25px 5px 25px 5px",
            objectFit: "scale-down",
            transition: "all 0.5s ease-in"
        },
        rootDesktop: {
            boxSizing: "border-box",
            width: "100%",
            height: "250px",
            backgroundColor: "white",
            border: "1px solid black",
            transition: "all 2s ease-out"

        },
        imageDesktop: {
            boxSizing: "border-box",
            width: "100%",
            height: "100%",
            padding: " 25px 5px 25px 5px",
            objectFit: "scale-down",
            transition: "all 0.5s ease-out"
        },
        imagesThumbsList: {
            boxSizing: "border-box",
            width: "100%",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            overflowX: "auto",
            height: "75px",
            transition: "all 2s ease-out",
            backgroundColor: "white",

            justifyContent: "stretch",
        },
        imagesThumbs: {
            padding: "0px 0px 20px 20px",
            alignSelf: "center",
            justifySelf: "center",
            boxSizing: "border-box",
            alignSelf: "center",
            width: "125px",
            height: "55px",
            objectFit: "scale-down",
            transition: "all 2s ease-out"
        },
        imagesGallery: {
            position: "fixed",
            top: "48px",
            bottom:"10%",
            boxSizing: "border-box",
            width: "100%",
            height:"84%",
            transition: "all 0.5s ease-in-out",
            backgroundColor: "white",
            objectFit: "contain",
            zIndex: 1
        },
        imagesGalleryThubms: {
            position: "fixed",
            boxSizing: "border-box",
            width: "100%",
            height: "10%",
            bottom: 0,
            zIndex: 1,
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            overflowX: "auto",
            transition: "all 2s ease-out",
            backgroundColor: "white",
            justifyContent: "stretch",
        }
    }


    return (<>
        {galleryView ? (<>
            <div
                style={width < 550 ? styles.root : styles.rootDesktop, { height: "100%" }}>
                <img
                    onContextMenu={(e) => e.preventDefault()}
                    onClick={() => setGalleryView(false)}
                    src={choosenImage} style={width < 550 ? styles.imagesGallery : styles.imagesGallery} alt={choosenImage} />
            </div>
            <div style={styles.imagesGalleryThubms}>
                {Object.keys(images).map(key => (<img 
                onContextMenu={(e) => e.preventDefault()}
                src={images[key]} onClick={() => setChoosenImage(images[key])} style={styles.imagesThumbs} />))}
            </div>

        </>) : (<><div
            style={width < 550 ? styles.root : styles.rootDesktop}>
            <img
                onContextMenu={(e) => e.preventDefault()}
                src={choosenImage} style={width < 550 ? styles.image : styles.imageDesktop} alt={choosenImage}
                onClick={() => setGalleryView(true)}

            />
        </div>
            <div style={styles.imagesThumbsList}>
                {Object.keys(images).map(key => (<img 
                onContextMenu={(e) => e.preventDefault()}
                src={images[key]} onClick={() => setChoosenImage(images[key])} style={styles.imagesThumbs} />))}
            </div></>)}
    </>
    );
};

export default ProductImageSlideThumbs;