import orderServices from "../../services/api/orders";
import ordersConstatnts from "./constants";

const loading = (bool) => {
    return {
        type: ordersConstatnts.ORDERS_LOADING,
        isLoading: bool,
    };
};

const addToDB = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await orderServices.addOrder(params);
        dispatch({
            type: ordersConstatnts.ORDERS_ITEM_ADD_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const getOrderList = () => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await orderServices.getOrders();
        dispatch({
            type: ordersConstatnts.ORDERS_ITEMS_FETCH_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const ordersActions = {
    getOrderList,
    addToDB,
}

export default ordersActions;