import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import icon from '../../../assets/icons';

const styles = {
    root: {
        marginTop: 0,
        display: "flex",
        flexDirection: "column",
        textAlign: "center",
        boxSizing: "border-box",
        width: "100vw",

    },
    rootDesktop: {
        marginTop: 0,
        display: "flex",
        flexDirection: "row",
        textAlign: "center",
        boxSizing: "border-box",
        width: "100vw",

    },
    container: {
        boxSizing: "border-box",
        width: "100%",
        height: "200px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-evenly",
        backgroundColor: "white",
        borderRight: "1px solid black"
    },
    containerDesktop: {
        boxSizing: "border-box",
        width: "33.333%",
        height: "200px",
        display: "flex",
        flexDirection: "column",
        flexWrap: "wrap",
        justifyContent: "space-evenly",
        backgroundColor: "white",
        borderBottom: "1px solid black",
        textAlign: "center",
    },
    iconStyle: {
        alignContent: "center",
        alignSelf: "center",
        fontSize: "48px",
        color: "black"
    },
    iconStyleDesktop: {
        textAling: "center",
        alignSelf: "center",
        fontSize: "48px",
        color: "black"
    }
}

const data = {
    "Product guarantee": [icon.faGraduationCap, "24 months", "Each product have 2 years of standard guarantee. You can expand quarantee for next 5 years."],
    "Shipping Details": [icon.faShippingFast, "Free Shipping", "You will recive your order in 2 work days."],
    "Order Return": [icon.faUndo, "14 days", "You have 2 weeks for cancelling and returning your purchases."],
}


const Details = ({ width }) => {
    const renderDetails = (detailData) => (
        <div onContextMenu={(e) => e.preventDefault()} style={width < 550 ? styles.root : styles.rootDesktop}>
            {Object.keys(detailData).map(key => (
                <div style={width < 550 ? styles.container : styles.containerDesktop}>
                    <FontAwesomeIcon
                        style={width < 550 ? styles.iconStyle : styles.iconStyleDesktop}
                        icon={detailData[key][0]} />
                    <h1>{detailData[key][1]} </h1>
                    <h2>{detailData[key][2]}</h2>
                </div>
            ))}
        </div>
    );


    return (<>
        {renderDetails(data)}
    </>
    )
}

export default Details;
