import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ProductsList from "../components/atoms/ProductCart/ProductsList";
import productsActions from '../store/products/action';
import categoriesActions from '../store/categories/action';
import { routes } from '../routes';

const Products = ({ products, getProducts, categories, getCategories, width, reload }) => {
    // const [reload,setReload] = useState(false)
    // const RELOAD = () => {
    //     setReload((state)=>!state)
    // };

    useEffect(() => { getProducts("limit=100"); getCategories() }, [reload])
    const categoriesData = categories.data;
    const productsData = products.data;
    return (

        <div className={"container"}>

            {Object.keys(productsData).map(key => <> {
                <ProductsList
                    key={productsData[key]._id}
                    products={productsData}
                    item={key}
                    width={width}
                    reload={reload}
                />
            }</>)}


        </div>

    )
}

Products.propTypes = {
    products: PropTypes.func.isRequired,
    // getProducts: PropTypes.func.isRequired
}
const getData = { getProducts: productsActions.getAllProducts, getCategories: categoriesActions.getAllCategories }


const mapStateToProps = (state) => {
    const { categories, products } = state;
    console.log(state);
    return { categories, products };
}
export default connect(mapStateToProps, getData)(Products);
