import api from "./api";

const getAllLocations = async (getUrlParams) => {
  const result = await api.get(`/locations?${getUrlParams}`);
  const { data } = result;
  return data;
};
const getAllLocationsFilters = async () => {
  const result = await api.get(`/locations?`);
  const { data } = result;
  return data;
};
const createLocation = async (params) => {
  const result = await api.post(`/locations/`, params );
  return result;
};
const updateLocation = async (params) => {
  const result = await api.patch(`/locations/${params._id}`, params );
  return result;
};
const deleteLocation = async (params) => {
  const result = await api.delete(`/locations/${params._id}`);
  return result;
};

const locationServices = {
  getAllLocations,
  getAllLocationsFilters,
  createLocation,
  updateLocation,
  deleteLocation,
};

export default locationServices;


