
import { faCloudShowersHeavy } from "@fortawesome/free-solid-svg-icons";
import React, { useState, useEffect } from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import AppBar from "../components/atoms/AppBar/AppBar";
import AutoScrollBanner from "../components/atoms/AutoScrollBanner/AutoScrollBanner";
import CartFooter from "../components/atoms/cartFooter/cartFooter";
import Details from "../components/atoms/details/Details";
import ImageSlider from "../components/atoms/ImageSlider/ImageSlide";
import PromoBar from "../components/atoms/input/promoBar/PromoBar";
import { MainMenu } from "../components/molecules/MainMenu";
import { styles } from "../components/oganisms/Layout/Layout";
import { sliderGalery } from "../assets/data/images";
import { routes } from "../routes/index";
import Products from "./Products";
import { fakeProducts } from '../assets/data/products'
import productServices from "../services/api/products";
import CartItem from "../components/atoms/cart/CartItem";
import Cart from "./Cart";
import Orders from "./Orders";
import { FSV } from "../components/atoms/ProductCart/fsView";
import Mapa from "./Map";
// import {basket} from "../assets/data/basket"

function App() {
  const [cart, setCart] = useState([]);
  const [products, setProducts] = useState([]);
  const [width, setWidth] = useState(window.innerWidth);
  const [mobileView, setMobileView] = useState(Boolean);
  const [latitude, setLatitude] = useState('')
  const [longitude, setLongitude] = useState('')
  const [openMenu, setMenuOpen] = useState(false)
  const [profileOpen, setProfileOpen] = useState(false)
  const [cartOpen, setCartOpen] = useState(false)
  const [specialOffer, setSpecialOffer] = useState(false)
  console.log("width", width)

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((position, error) => {
      setLatitude(position.coords.latitude);
      setLongitude(position.coords.longitude);
    });
  }, [])

  useEffect(() => {
    const RESIZE = () => {
      setWidth(window.innerWidth);
    };
    window.addEventListener("resize", RESIZE);
    return () => window.removeEventListener("resize", RESIZE);
  }, []);

  const [reload, setReload] = useState(false)
  const RELOAD = () => {
    setReload((state) => !state)
  };
  console.log(reload)

  return (<>
    <div style={styles}>
      <BrowserRouter>
        {specialOffer ? <PromoBar /> : <></>}
        <AppBar
          setMenuOpen={setMenuOpen}
          openMenu={openMenu}
          profileOpen={profileOpen}
          setProfileOpen={setProfileOpen}
          cartOpen={cartOpen}
          setCartOpen={setCartOpen}
          width={width}
          reload={RELOAD}
        />
        {openMenu ? (<MainMenu setMenuOpen={setMenuOpen} openMenu={openMenu} width={width} />) : (<></>)}
        <Switch>
          <Route path={routes.about}>
            <h1>about</h1>
          </Route>
          <Route path={routes.cart}>
            <Cart width={width} reload={RELOAD} />
            {/* {cartOpen ? <Cart width={width} reload={RELOAD} /> : <Redirect to={routes.products} />} */}
          </Route>
          <Route path={routes.fileSystem}>
          
          </Route>
          <Route path={routes.orders}>
            <Orders width={width} reload={RELOAD} />
          </Route>
          <Route path={routes.products}>
            <Products
              cart={cart}
              setCart={setCart}
              width={width}
              reload={RELOAD}
            />
          </Route>
          <Route path={routes.privacy}>
            <FSV />

          </Route>
          <Route path={routes.profile}>
            <h1>profile</h1>

          </Route>
          <Route path={routes.map}>
            <Mapa />
          </Route>
          {/* <h1>home</h1> */}
          <Route path={routes.home}>
            <AutoScrollBanner />
            <ImageSlider width={width} images={sliderGalery} delayTime={1000} />
            {/* <AutoScrollBanner /> */}
            <Details width={width} />
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  </>
  );
}

export default App;
