import React,{useState} from 'react';
const styles = {
    inputInactive: {
        width: "80%",
        height: "32px",
        marginTop: 5,
        backgroundColor: "black",
        border: "1px solid white"
        
    },
    inputActive: {
        width: "80%",
        height: "32px",
        marginTop: 5,
        backgroundColor: "white",
        border: "1px solid white"
    }

}

const data = 'hbhviavkhfakhvfakhvfkhvfakhvfa'

const SearchContainer = () => { return(<>
<div style={{position:"fixed",top:48,bottom:0,left:0,right:0,backgroundColor:"red"}}>
{data}
</div>
</>)}

const Input = ({ defaultValue, placeholder, width }) => {
    const [inputActive, setInputActive] = useState(false)
    const [value, setValue] = useState()
    // console.log(value)
    const inputToggle = () => {
        setInputActive((state)=>!state);
    }
    return (<>
        <input onClick={()=>inputToggle()} type="text" style={inputActive ? styles.inputInactive : styles.inputActive} onChange={(e)=>setValue(e.target.value)} value={value} defaultValue={defaultValue} placeholder={placeholder} />
        {inputActive ? <SearchContainer/> : <></>}
    </>)
}
export default Input;