import api from "./api";
const getCart = async () => {
  const result = await api.get(`/cart`);
  const { data } = result;
  return data;
};

const createProduct = async (params) => {
  const result = await api.post(`/cart/`, params );
  return result;
};

const updateCart = async (id,params) => {
  const result = await api.patch(`/cart/${id}`,params);
  return result;
};

const deleteProduct = async (params) => {
  const result = await api.delete(`/cart/${params}`);
  return result;
};

const deleteCart = async () => {
  const result = await api.delete(`/cart/`);
  return result;
};

const cartServices = {
  getCart,
  updateCart,
  createProduct,
  deleteProduct,
  deleteCart,
};


export default cartServices;