import productsConstatnts from './constants';

let initialState = {
    data: {},
    isLoading: false,
};

const loadProducts = (state, action) => {
    const { payload, page } = action;
    let newState = {};
    if (page > 0) {
        newState = Object.assign({}, state);
    } else {
        newState = { data: {} };
    }
    for (const key in payload) {
        newState.data[payload[key]._id] = payload[key];
    }
    return newState;
};

export default (state = initialState, action) => {
    switch (action.type) {
        case productsConstatnts.PRODUCTS_FETCH_SUCCESS:
            return loadProducts(state, action);
    }
    return state;
}