import React, { useEffect } from 'react';
import JconButton from '../iconButton/iconButton';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import icon from '../../../assets/icons';
import ProductImageSlide from './ProductImageSlide';


export const ProductCart = ({ item, products, reverseCart, addToCart }) => {
  return (
    <div key={products[item]._id} ><FontAwesomeIcon onClick={() => reverseCart(true)} className={"cart--reverse--button"} icon={icon.faInfoCircle} size={32} />
      <ProductImageSlide onClick={() => reverseCart(true)} delayTime={2000} images={products[item].Images} />
      <div className="cart--info">
        <div className="cart--info--item">
          {products[item].Name}
        </div>
      </div>
      <div className="cart--actions">
        <h1 style={{ alignSelf: "center", marginRight: 10 }}>Price: {products[item].Unit_price}</h1>
        <div className="cart--actions--item">
          <JconButton
            variant={"row"}
            icon={icon.faShoppingCart}
            label={"Add to Cart"}
            onClick={() => addToCart()}
            border={0}
            iconAlign={"left"}
            background={"green"}
            color={"white"}
            borderRadius={"10px 0px 10px 0px"}
            padding={5}
          />
        </div>
      </div></div>
  )
}

export default ProductCart;