import React, { useEffect, useState } from "react";

const ImageSlider = ({ images, delayTime, width }) => {

    const [index, setIndex] = useState(0);
    const [autoPlayTimeout, setAutoPlayTimeout] = useState(delayTime)

    const slideLeft = () => {
        const nextIndex = index - 1;
        if (nextIndex < 0) {
            setIndex(images.length - 1);
        } else {
            setIndex(nextIndex);
        }
    };

    const autoPlay = () => {
        setTimeout(slideLeft, autoPlayTimeout);
    }

    useEffect(() => {
        autoPlay();
    })
    const styles = {
        root: {
            boxSizing: "border-box",
            width: "100vw",
            height: "275px",
            backgroundColor: "white",
            transition: "all 2s ease-out",
            border: "1px solid black",
        },
        image: {
            boxSizing: "border-box",
            width: "100vw",
            height: "275px",
            padding: " 25px 5px 25px 5px",
            objectFit: "scale-down",
            transition: "all 2s ease-in"
        },
        rootDesktop: {
            boxSizing: "border-box",
            width: "100vw",
            height: "100%",
            backgroundColor: "white",
            border: "1px solid black",
            transition: "all 2s ease-out"

        },
        imageDesktop: {
            boxSizing: "border-box",
            width: "100%",
            height: "475px",
            padding: " 25px 5px 25px 5px",
            objectFit: "scale-down",
            transition: "all 2s ease-out"
        }
    }
    return (
        <div
            style={width < 550 ? styles.root : styles.rootDesktop}>
            <img
                onContextMenu={(e) => e.preventDefault()}
                onMouseDown={() => console.log("mouse right click")}
                src={images[index]} style={width < 550 ? styles.image : styles.imageDesktop} alt={index} />
        </div>
    );
};

export default ImageSlider;