import api from "./api";
const getOrders = async () => {
  const result = await api.get(`/order`);
  const { data } = result;
  return data;
};

const addOrder = async (params) => {
  const result = await api.post(`/order/`, params );
  return result;
};

const updateOrder = async (id,params) => {
  const result = await api.patch(`/order/${id}`,params);
  return result;
};

const deleteOrder = async (params) => {
  const result = await api.delete(`/order/${params}`);
  return result;
};


const orderServices = {
  getOrders,
  updateOrder,
  addOrder,
  deleteOrder,
};


export default orderServices;