import cartServices from "../../services/api/cart";
import cartConstatnts from "./constants";

const loading = (bool) => {
    return {
        type: cartConstatnts.CART_LOADING,
        isLoading: bool,
    };
};

const addToCart = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await cartServices.createProduct(params);
        dispatch({
            type: cartConstatnts.CART_ITEM_ADD_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const getCartItems = () => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await cartServices.getCart();
        dispatch({
            type: cartConstatnts.CART_ITEMS_FETCH_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const cartActions = {
    getCartItems,
    addToCart,
}

export default cartActions;