const AutoScrollBanner = () => {
    const listContainer = {
        height: "50px",
        boxSizing:"borde-box",
        backgroundColor:"white",
        border: "1px solid silver",
        // animation: " bannermove 5s linear infinite",
    }
    const listItemsContainer = {
        animation: " bannermove2 15s linear infinite",
        width: "300vw",
        display: "flex",
        flexDirection: "row",
        flexWrap: "nowrap",
        justifyContent:"space-evenly",
        boxSizing:"borde-box",
        overflowX:"scroll",
        
    }

    const renred_ListItem = (listData) => (
        <div style={listContainer}>
            <div style={listItemsContainer}>
            {Object.keys(listData).map(key => (
                // console.log(key, listData[key]),
                <img 
                onContextMenu={(e) => e.preventDefault()}
                style={{ width:400, height: 50, objectFit: "scale-down",justifyContent:"stretch",}}
                src={listData[key]} />
                ))}
                
                </div>
        </div>
    );

    return (<>
        {renred_ListItem(Data)}
    </>)
};

export default AutoScrollBanner;