import locationServices from "../../services/api/locations";
import locationsConstatnts from "./constants";

const loading = (bool) => {
    return {
        type: locationsConstatnts.LOCATIONS_LOADING,
        isLoading: bool,
    };
};

const addLocation = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await locationServices.createLocation(params);
        dispatch({
            type: locationsConstatnts.LOCATION_CREATE_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const getAllLocations = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await locationServices.getAllLocations(params);
        dispatch({
            type: locationsConstatnts.LOCATIONS_FETCH_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const LocationsActions = {
    getAllLocations,
    addLocation,
}

export default LocationsActions;


