import productServices from "../../services/api/products";
import categoriesConstants from "./constants";


const loading = (bool) => {
    return {
        type: categoriesConstants.CATEGORIES_LOADING,
        isLoading: bool,
    };
};

const getAllCategories = (params) => {
    return async (dispatch) => {
        dispatch(loading(true));
        const result = await productServices.getCategories(params);
        dispatch({
            type: categoriesConstants.CATEGORIES_FETCH_SUCCESS,
            payload: result
        });
        dispatch(loading(false));
    };
};

const categoriesActions = {
    getAllCategories,
}

export default categoriesActions;


