import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ordersActions from '../store/orders/action';
import OrdersList from '../components/atoms/orders/OrdersList';

const Orders = ({ orders, getOrders, width, reload }) => {
    useEffect(() => { getOrders()}, [reload])
    const ordersData = orders.data;
    console.log("ordersData[0].products",ordersData)
    return (
        <OrdersList
            ordersData={ordersData}
            reload={reload}
            width={width}
        />
    )
}

Orders.propTypes = {
    orders: PropTypes.func.isRequired,
}
const getData = { getOrders: ordersActions.getOrderList }

const mapStateToProps = (state) => {
    const { orders } = state;
    return { orders };
}

export default connect(mapStateToProps, getData)(Orders);
