import ordersConstatnts from './constants';

let initialState = {
    data: {},
    isLoading: false,
};

const loadOrders = (state, action) => {
    const { payload } = action;
    let newState = {};
    if (payload > 0) {
        newState = Object.assign({}, state);
    } else {
        newState = { data: {} };
    }
    for (const key in payload) {
        newState.data[payload[key]._id] = payload[key];
    }
    return newState;
};
const addOrderToDB = (state,action) => {}

export default (state = initialState, action) => {
    switch (action.type) {
        case ordersConstatnts.ORDERS_ITEMS_FETCH_SUCCESS:
            return loadOrders(state, action);
        case ordersConstatnts.ORDERS_ITEM_ADD_SUCCESS:
            return addOrderToDB(state, action);
    }
    return state;
}