import api from "./api";
const getCategories = async () => {
  const result = await api.get(`/category`);
  const { data } = result;
  return data;
};
const createCategory = async (params) => {
  const result = await api.post(`/category/`, params );
  return result;
};
const getAllProducts = async (getUrlParams) => {
  const result = await api.get(`/products?${getUrlParams}`);
  const { data } = result;
  console.log("getAllProducts")
  return data;
};
const createProduct = async (params) => {
  const result = await api.post(`/products/`, params );
  return result;
};
const updateProduct = async (params) => {
  const result = await api.patch(`/products/${params._id}`, params );
  return result;
};
const deleteProduct = async (params) => {
  const result = await api.delete(`/products/${params._id}`);
  return result;
};

const productServices = {
  getAllProducts,
  getCategories,
  createCategory,
  createProduct,
  updateProduct,
  deleteProduct,
};


console.log(getAllProducts)
export default productServices;