import React, { useEffect, useState } from 'react';
import icon from '../../../assets/icons/index.js';
import JconButton from '../iconButton/iconButton.jsx'
import '../iconButton/iconButton.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import '../../../assets/fonts/fonts.css'
import { Link, Redirect } from 'react-router-dom';
import Input from '../input/Input.jsx';


import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import cartActions from '../../../store/cart/action';
import { routes } from '../../../routes/index.js';
import { broweserHistoryBack } from '../../../globals/index.js';


const styles = {
    button: { width: "48px", height: "48px" }
}
const AppBar = ({ cart, getCart, setMenuOpen, openMenu, profileOpen, setProfileOpen, cartOpen, setCartOpen, width }) => {
    
    const cartData = cart.data;
    const [cartLength,setCartLength] = useState(0);
    Object.size = function(obj) {
        var size = 0,
          key;
        for (key in obj) {
          if (obj.hasOwnProperty(key)) size++;
        }
        return size;
      };
      useEffect(() => { 
        getCart();
          setCartLength(() => Object.size(cartData))    
    }, []) 



    // console.log(cartLength)
    return (<>
        <div style={{ position:"sticky", top:0,  width: "100vw", height: "48px", display: "flex", flexDirection: "row", justifyContent: "stretch", backgroundColor: "black", color: "whitesmoke", zIndex:0 }}>

            <div style={{ width: "50%", height: "48px", display: "flex", flexDirection: "row", justifyContent: "flex-start" }} >
                {openMenu ? (
                    <JconButton
                        onClick={() => setMenuOpen(false)}
                        icon={icon.faBars}
                        iconAlign="center"
                        // label="Close Menu"
                        labelAlign="center"
                        size="24px"
                        fontsize="8"
                        variant="column"
                        className="ToggleRotate"
                        rotate="360"

                    />) : (
                        <JconButton
                            onClick={() => {setMenuOpen(true);setCartOpen(false);setProfileOpen(false)}}
                            icon={icon.faBars}
                            iconAlign="center"
                            // label="Open Menu"
                            labelAlign="center"
                            size="24px"
                            fontsize="8"
                            variant="column"
                            className="Toggle"

                        />
                    )}
                <p style={{ width: "10%" }}></p>

                {/* title */}
                <Link to={"/home"} style={{ display: "flex", height: 48, justifyContent: "space-around", textDecoration: "none", color: "whitesmoke" }} >
                    <FontAwesomeIcon style={{ alignSelf: "center", fontSize: "22px" }} icon={icon.faHome} />
                    <p style={{ width: 10 }}></p>
                    {width<650?(<div style={{ display: "flex", flexDirection: "column", lineHeight: '16px', marginTop: 9 }}>
                        <p style={{ fontSize: "14px", letterSpacing: 3, alignSelf: "left" }}>smart</p>
                        <p style={{ fontSize: "18px", letterSpacing: 3, alignSelf: "left" }}>controls</p>
                    </div>):(<div style={{ display: "flex", flexDirection: "column", lineHeight: '48px' }}>
                        <p style={{ fontSize: "32px", letterSpacing: 3, alignSelf: "left" }}>smart controls</p>
                    </div>)}
                </Link>
            </div>


            <div style={{ width: "50%", height: "48px", display: "flex", flexDirection: "row", justifyContent: "flex-end" }} >
                <Input placeholder="Search website..." />
                <p style={{width:'10px'}}></p>
                {profileOpen ? (<JconButton
                    onClick={() => {setProfileOpen(false);broweserHistoryBack()}}
                    icon={icon.faUser}
                    iconAlign="center"
                    // label="Open Menu"
                    labelAlign="center"
                    size="24px"
                    fontsize="8"
                    variant="column"
                    className="ToggleActive"
                />) : (<JconButton
                    onClick={() => {setProfileOpen(true);setCartOpen(false);setMenuOpen(false)}}
                    icon={icon.faUser}
                    iconAlign="center"
                    // label="Open Menu"
                    labelAlign="center"
                    size="24px"
                    fontsize="8"
                    variant="column"
                    className="Toggle"
                    href="/profile"
                />)}
                {cartOpen ? (<><JconButton
                    onClick={() => {setCartOpen(false);broweserHistoryBack()}}
                    icon={icon.faShoppingCart}
                    iconAlign="center"
                    // label="Open Menu"
                    labelAlign="center"
                    size="24px"
                    fontsize="8"
                    variant="column"
                    className="ToggleActive"
                    label={cartLength}
                /><Redirect to={routes.cart}/></>) : (<JconButton
                    onClick={() => {setCartOpen(true)}}
                    icon={icon.faShoppingCart}
                    iconAlign="center"
                    // label="Open Menu"
                    labelAlign="center"
                    size="24px"
                    fontsize="8"
                    variant="column"
                    className="Toggle"
                    href="/cart"
                    label={cartLength}
                    lstyle={{ position:"absolute", bottom:2.5, left:2.5, backgroundColor:"yellow",color:"black",borderRadius:25,width:15,height:15}}
                />)}
            </div>
        </div>
    </>)
}
// export default AppBar 
AppBar.propTypes = {
    cart: PropTypes.func.isRequired,
}
const getData = { getCart: cartActions.getCartItems }


const mapStateToProps = (state) => {
    const { cart } = state;
    console.log(state);
    return { cart };
}
export default connect(mapStateToProps, getData)(AppBar);